.fitted-distribution-rect {
    fill: none;
    stroke: none;
}

.selected-fitted-distribution-rect {
    stroke: black;
    stroke-width: 2;
    stroke-dasharray: 4;
}

.prior-result-div {
    width: 90%;
    display: flex;
    flex-direction: column;
    border-top: 1px solid black;
    overflow-y: auto;
}

.prior-result-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #bbb;
}